<template>
  <div class="menu">
    <el-menu
      class="el-menu-vertical-demo"
      @open="handleOpen"
      @close="handleClose"
      background-color="#293749"
      text-color="#a9b9c9"
      active-text-color="#fff"
    >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>插件列表</span>
        </template>
        <el-menu-item index="1-1" @click="changeroute('shopDoceration')"
          >店铺装修</el-menu-item
        >
        <el-menu-item index="1-2" @click="changeroute('index')"
          >插件表</el-menu-item
        >
        <el-menu-item index="1-3">插件预览表</el-menu-item>
        <el-menu-item index="1-4">插件预览表</el-menu-item>
        <el-menu-item index="1-5">插件预览表</el-menu-item>
      </el-submenu>
      <el-submenu index="2">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>插件列表</span>
        </template>
        <el-menu-item index="2-1">插件预览表</el-menu-item>
        <el-menu-item index="2-2">插件预览表</el-menu-item>
        <el-menu-item index="2-3">插件预览表</el-menu-item>
        <el-menu-item index="2-4">插件预览表</el-menu-item>
        <el-menu-item index="2-5">插件预览表</el-menu-item>
      </el-submenu>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: "menulist",
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    changeroute(val) {
      this.$router.push(val);
    },
  },
};
</script>
<style lang="less" scoped>
.menu {
  width: 100%;
  height: 100%;
  background: #293749;
  color: #ddd;
}
.el-menu {
  border: none;
}
</style>


