import Vue from 'vue'
import Router from 'vue-router'
import { Message } from "element-ui"
import index from '@/view/index/index'
import login from '@/view/login/index'
import functionList from '@/view/functionList/index'
import shopDoceration from '@/view/shopDoceration/index'


const originalPush = Router.prototype.push
Router.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}
Vue.use(Router)
let routes = [
  {
    path: '/login',
    name: 'login',
    component: login,
  },
  {
    path: '/',
    // name: '',
    component: index
  },
  {
    path: '/index',
    component: index,
    children: [
      {
        path: "/",
        name: "functionList",
        component: functionList
      },
      {
        path: "/shopDoceration",
        name: "shopDoceration",
        component: shopDoceration
      }
    ]
  },
]
let router = new Router({
  routes
})
router.beforeEach((to, form, next) => {
  let token = sessionStorage.getItem("token")
  if (to.name == "login") {
    return next()
  }
  if (token) {
    return next()
  }
  if (!token) {
    console.log(Message("用户信息丢失，请重新登录"))
    return next('/login')
  }
})
// router.afterEach((to, from) => {
//   if (to.path == '/login') {
//     window.location.reload();
//   }
// })
export default router



