<template>
  <div class="index">
    <el-container>
      <el-aside width="200px">
        <menulist></menulist>
      </el-aside>
      <el-container>
        <el-header>
          <headerlist></headerlist>
        </el-header>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script>
import menulist from "./menuList";
import headerlist from "./headerList";
export default {
  name: "index",
  data() {
    return {};
  },
  components: { menulist, headerlist },
};
</script>
<style lang="less">
.index {
  width: 100%;
  .el-container {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    .el-aside {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
    }
    .el-container {
      width: 100%;
      height: 100%;
      margin: 0;
      padding: 0;
      .el-header {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
      }
      .el-main {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
      }
    }
  }
}
</style>



