<template>
  <div class="login" v-loading="loading">
    <div class="loginView">
      <div class="inputView">
        <el-input
          v-model="userMessage.username"
          placeholder="请输入账号"
        ></el-input>
        <el-input
          v-model="userMessage.password"
          placeholder="请输入密码"
          show-password
        ></el-input>
      </div>
      <el-button type="primary" class="enter" @click="enter()">登录</el-button>
      <div class="btnView">
        <el-button type="text" @click="qwer()">修改密码</el-button>
        <el-button type="text">找回密码</el-button>
        <el-button class="fr" type="text" @click="regView = true"
          >注册</el-button
        >
      </div>
    </div>
    <el-dialog title="注册账号" :visible.sync="regView" width="700px">
      <el-form
        :model="regForm"
        label-width="80px"
        :rules="regRules"
        ref="regForm"
      >
        <el-form-item
          label="用户名"
          :label-width="formLabelWidth"
          prop="userName"
        >
          <el-input v-model="regForm.userName" width="100"></el-input>
        </el-form-item>
        <el-form-item
          label="密码"
          :label-width="formLabelWidth"
          prop="password"
        >
          <el-input v-model="regForm.password" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item
          label="确认密码"
          :label-width="formLabelWidth"
          prop="confirmPassword"
        >
          <el-input
            v-model="regForm.confirmPassword"
            autocomplete="off"
            @change="samePassword"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="regView = false">取 消</el-button>
        <el-button type="primary" @click="samePassword">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "login",
  data() {
    return {
      loading: false,
      // 用户信息
      userMessage: {
        username: "",
        password: "",
      },
      regView: false,
      regForm: {
        userName: "",
        password: "",
        confirmPassword: "",
      },
      regRules: {
        userName: [
          {
            required: true,
            message: "仅支持数字或字母,长度在 6 到 16 个字符",
            pattern: /[A-Za-z0-9]{6,16}/,
            trigger: "change",
          },
        ],
        password: [
          {
            required: true,
            message: "仅支持数字或字母,长度在 6 到 16 个字符",
            trigger: "change",
            pattern: /[A-Za-z0-9]{6,16}/,
          },
        ],
        confirmPassword: [
          {
            required: true,
            message: "仅支持数字或字母,长度在 6 到 16 个字符",
            trigger: "change",
            pattern: /[A-Za-z0-9]{6,16}/,
          },
        ],
      },
      formLabelWidth: "120px",
    };
  },
  methods: {
    // 注册时密码与再次输入的密码相同
    samePassword() {
      this.$refs["regForm"].validate((valid) => {
        if (valid) {
          this.regView = false;
          this.loading = true;
          this.registered();
        } else {
          return false;
        }
      });
    },
    // 登录
    async enter() {
      let data = this.userMessage;
      let { body, msg, code } = await this.$http.post("/login", data);
      if (code == "10000") {
        sessionStorage.setItem("token", body.token);
        this.$router.push("/index");
      } else {
        this.$message(msg);
      }
    },
    // 注册
    async registered() {
      let data = this.regForm;

      this.loading = false;
      let { body, msg, code } = await this.$http.post("/createAccount", data);
      if (code == "10000") {
      } else {
      }
    },
  },
};
</script>
<style lang="less" scoped>
.login {
  width: 100%;
  .loginView {
    width: 300px;
    float: right;
    margin-right: 20%;
    margin-top: 20%;
    .enter {
      width: 100%;
    }
    .inputView {
      .el-input {
        margin-bottom: 10px;
      }
    }
  }
}
</style>


