import axios from "axios";
import Qs from 'qs'
import router from '../router';
// import md5 from 'MD5'
const instance = axios.create({
    baseURL: "http://localhost:3000",
    timeout: 1000 * 10,
    headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
        'Access-Control-Allow-Origin': "*"
    }
})
let getHeader = function (url, code) {
    let timestamp = new Date() * 1
    let token = sessionStorage.getItem("token") || ""
    let source = "web"
    // let sign = md5(source + url + code + timestamp + token)
    let sign = source + url + code + timestamp + token
    return {
        'Content-Type': 'application/json; charset=UTF-8; application/x-www-form-urlencoded',
        'Access-Control-Allow-Origin': "*",
        'token': token,
        'timestamp': timestamp,
        'code': code,
        'sign': sign,
        'source': source,
    };
}
// 请求拦截
instance.interceptors.request.use(
    config => {
        let title = "请求接口"
        let configUrl = config.url
        let data = config.params || config.data
        console.log(title, configUrl, data)
        let url = config.url.replace(config.baseURL, '')
        let code = config.code
        config.headers = getHeader(url, code)
        return config;
    },
    error => {
        return Promise.error(error);
    }
)
// 响应拦截
instance.interceptors.response.use(
    response => {
        let title = ""
        let configUrl = response.config.url
        let data = response.data
        if (response.status == "200") {
            title = "请求成功"
        } else {
            title = "请求失败"
        }
        console.log(title, configUrl, data)
        return response
    },
    error => {
        return Promise.reject(error);
    }
)
// post请求
let post = function (url, params) {
    return request(url, params, 'post')
}
// get请求
let get = function (url, params) {
    return request(url, params, 'get')
}
// 发出请求
let request = function (url, params, type) {
    let data = {}
    if (type == "get") {
        data = {
            'params': params
        }
    }
    else {
        data = params
    }
    return new Promise((resolve, reject) => {
        instance[type](url, data)
            .then(
                response => {
                    resolve(response.data)
                }
            )
            .catch(
                error => {
                    reject(error.data)
                }
            )
    })
}
let http = {
    post,
    get
}
export default http;


